.navBarItems{
   background-color: #fff;
   display:flex;
   justify-content: space-between;
   align-items: center;
   padding: 0 30px;
   box-shadow: 0 5px 15px 0 rgba(0,0,0,.25);
   width: 95%;
   height: 80px;
   border-radius: 13px;
   position:fixed;
   top:20px;
   left: 50%;
   transform: translate(-50%);
   z-index: 999;
}

.navBarItemsHide{
   display: none;
}

.navbarLogo{
   color:#222;
   font-size: 2rem;
   cursor: pointer; 
   white-space: nowrap;
}

.navMenu{
   display: grid;
   grid-template-columns: repeat(6,auto);
   list-style: none;
   align-items: center;
   gap: 10px;
}

.nav-links{
   text-decoration: none;
   color: #222;
   font-size: 1.2rem;
   font-weight: 600;
   padding: 0.7rem 1rem;
   white-space: nowrap;
}

.nav-links i{
   padding-right: 10px;
}

.nav-links:hover{
   background-color: #01959a;
   color: #fff;
   border-radius: 4px;
   transition: all 0.2s ease-in-out;
}

.nav-links-mobile{
   display: none;
}

.buttonStyle{
   padding: 0.5re, 1rem;
   white-space: nowrap;
   border-radius: 0.3rem;
   font-size: 1rem;
   font-weight: 600;
   border: none;
   cursor: pointer;
   transition: 0.2s ease-in-out;
}

.buttonStyle:hover{
   background-color: #151516;
   color:#fff;
}

.menuIcons{
   display: none;
}

.logoContainer{
   display: flex;   
   height: 100%;
   width: 20vw;
}

.logo{
   width: 100%;
   height: 100%;
}

@media screen and (max-width:850px){
   .navBarItems{
       z-index: 99;
   }
   .navMenu{
       display: flex;
       flex-direction: column;
       justify-content:flex-start;
       background-color: #fff;
       width: 100%;
       height: auto;
       position: absolute;
       top:0;
       left: -100%;
       opacity: 0;
       align-items: stretch;
       padding:80px 0 30px 0;
       margin: 0;
       transition: 0.3s ease-in-out;
       border-radius: 13px;
   }
   .navMenuActive{
       left:0;
       opacity: 1;
       z-index: -1;
       transition: 0.3s ease-in-out;
   }
   .nav-links{
       display:block;
       width: 100%;
       font-size: 1.2rem;
       padding: 2rem 0;
       text-align: center;
   }
   .nav-links:hover{
       background-color: #01959a;
       transition: none;
   }
   .nav-links-mobile{
       display: block;
       text-align: center;
       padding:1.5rem;
       margin: auto;
       border-radius: 4px;
       width: 80%;
       background-color: #01959a;
       color:#fff;
       font-size: 1.2rem;
       font-weight: 700;
       letter-spacing: 2px;
       text-decoration: none;
   }

   .buttonStyle{
       display: none;
   }

   .menuIcons{
       display: block;
       cursor: pointer;
   }

   .menuIcons i{
       font-size: 1.2rem;
       color:#222
   }
   .logoContainer{
      width: 50vw;
   }
}



