.underUnderHomecontainer{
    margin: 4rem 6rem;
    color:#2a2a2a;
}

.underUnderHomecontainer h1{
    font-size: 3rem;
    text-align: center;
}

.underUnderHomecontainer p{
   text-align: center;
}

.cardContainer{
    margin-top: 3rem;
    display: flex;
    justify-content: space-between; 
}

.cContainer{
    width: 32%;
    text-align: start;
    box-shadow: 0 5px 25px 2px rgba(0,0,0,.11);
    border-radius: 7px; 
    cursor: pointer;
    padding: 1rem .8rem;
}

.cImage{
    height: 200px;
    overflow: hidden;
    border-radius: 7px;
}

.cImage img{
    width: 100%;
    height: 100%;
    transition: 0.3s ease-in-out;
    border-radius: 7px;
}

.cImage:hover img{
    transform: scale(1.3);
}

.cContainer h4{
    font-size: 1.2rem;
    padding: .9rem 0 .2rem 0;
    text-align: center;
}

@media screen and (max-width:850px){
    .underUnderHomecontainer{
        margin: 4rem 2rem;
    }
    .cardContainer{ 
        flex-direction: column;
    }
    .cContainer{
        width: 100%;
        margin-bottom: 1.5rem;
    }
}